<template>
    <div class="container">
        <h3 class="mb-md">{{pageTitle}}</h3>
        <el-form :model="ruleForm" ref="ruleForm" class="demo-ruleForm">
            <el-row class="search-wrap mb-lg">
                <el-col :xs="24" :md="8">
                    <el-row :gutter="10">
                        <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l"></el-col>
                        <el-col :xs="24" :sm="20" :md="16">
                            <el-form-item prop="keywords">
                                <el-input v-model="ruleForm.keywords" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :xs="24" :md="8">
                    <el-row :gutter="10">
                        <el-col :xs="24" class="w92 f14 mb-md t-r t-xs-l"></el-col>
                        <el-col :xs="24" :sm="20" :md="16">
                            <el-form-item prop="applyDate">
                                <el-date-picker v-model="ruleForm.applyDate" type="daterange" placeholder="" style="width: 100%">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :span="24" class="t-r">
                    <el-button @click="onSearch('ruleForm')" type="primary"> </el-button>
                    <el-button @click="isClear('ruleForm')"></el-button>
                </el-col>
            </el-row>
        </el-form>
	
		<new-resume 
				@refreshResumes="ondispatch"
                v-if="type==0"
		></new-resume><!--  -->
		<resume-actions
				:dialogTitle="dialogTitle"
				:dialogContent="dialogContent"
				:approvalFormVisible="approvalFormVisible"
				:employFormVisible="employFormVisible"
				:jointalentFormVisible="jointalentFormVisible"
				:talentId="talentId"		
				:currentStatus="currentStatus"
				:approvalValue="approvalValue"
				@refreshResumes="ondispatch"
				@hideDialog="hideDialog"
		></resume-actions><!--  -->

        <el-row>
            <el-table :data="tableData" @filter-change="onFilter" border style="width: 100%">
                <el-table-column type="index" prop="index" label="" width="65"></el-table-column>
				<el-table-column prop="name" label="" width="100"></el-table-column>
				<el-table-column prop="phone" label="" width="130"></el-table-column>
				<el-table-column prop="profession" label="" width="160"></el-table-column>
				<el-table-column prop="sex" label="" width="65"></el-table-column>
				<el-table-column prop="age"label=""width="65"></el-table-column>
				<el-table-column prop="educational" label="" width=""></el-table-column>
				<el-table-column prop="work_time" label="" width="90"></el-table-column>
				<el-table-column prop="location" label="" width="90"></el-table-column>
				<el-table-column prop="person_type" label="" width="100" v-if="type==1"></el-table-column>
				<el-table-column 
						prop="status" 
						label="" 
						width="100"
						:filters="[
                                { text: '', value:1},
                                { text: '', value:2},
                                { text: '', value:3},
                                { text: '', value:4},
                                { text: '', value:5},
                            ]"
						filter-placement="bottom-end">			
				</el-table-column>
				<el-table-column label="" :width="type==1 ? 180 : 250">
					<template scope="scope">
						<div class="t-r">
							<el-button v-if="scope.row.status_value==1" @click="resumeAction(scope.row,true)" type="text" size="small">  </el-button>
							<el-button v-if="scope.row.status_value==2" @click="resumeAction(scope.row,true)" type="text" size="small">  </el-button>
							<el-button v-if="scope.row.status_value==3" @click="resumeAction(scope.row,true,'employ')" type="text" size="small">  </el-button>
							<el-button v-if="scope.row.status_value<4" @click="resumeAction(scope.row,false)" type="text" size="small">  </el-button>
							<el-button v-if="type!=1 && scope.row.status_value<4" @click="resumeAction(scope.row,true,'join-talent')" type="text" size="small">  </el-button>
							<el-button @click="Tolink(scope.row)" type="text" size="small">  </el-button>
						</div>
					</template>
				</el-table-column>
            </el-table>
        </el-row>
        <el-row class="t-r mt-lg">
            <el-pagination v-if="pagenation == 1"
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentpage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
            <el-pagination v-else small
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentpage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="pageSize"
                    layout="prev, pager, next"
                    :total="total">
            </el-pagination>
        </el-row>
	</div>	
</template>

<script>
    import { mapGetters } from 'vuex';
    import NewResume from '../../components/talent/newResume.vue';
    import ResumeActions from '../../components/talent/resumeActions.vue';

    export default {
        components: { NewResume, ResumeActions  },
        name:'talents',
        data(){
            return {
                ruleForm:{
                    keywords:'',
                    applyDate:''
                },
                type: 0,
                pageTitle: '',

                currentpage:1,
                pageSize:10,
                total:0,
                sort:'',
                filter:[],//
                keywords:'',
                start_time:'',
                end_time:'',

                approvalFormVisible: false,
                employFormVisible: false,
                jointalentFormVisible: false,
                dialogTitle: '',
                dialogContent: '',

                talentId: {type: Number},
                currentStatus: {type: Number},                
                approvalValue: {type: Boolean},    
            }
        },        
        computed:{
            ...mapGetters([
                'pagenation'
			]),
			tableData(){
				return this.$store.state.talent.resumes_table;
			}
        },
        created(){
        	this.type = this.$route.query.joined=='1' ? 1 : 0;
        	this.pageTitle = this.type==1 ? '' : '';
            this.$store.dispatch('Resumes_Table',{talent:this.type,page_size:10}).then(response=>{
                let data = response.data.data;
                this.currentpage = data.page.currentPage;
                this.total = data.page.totalCount;
            });
        },
        methods:{
        	//
            searchData(paged){
                if(paged != null){
                    this.currentpage = paged;
                }
                return {
                    talent:this.type,
                    page:this.currentpage,
                    page_size:this.pageSize,
                    status:this.filter,
                    keywords:this.keywords,
                    start_time:this.start_time,
                    end_time:this.end_time
                };
            },
            //
            ondispatch(){
                this.$store.dispatch('Resumes_Table',this.searchData()).then(response =>{
                        let data = response.data.data;
                        this.currentpage = data.page.currentPage;
                        this.total = data.page.totalCount;
                        this.pageSize = data.page.perPage;
                    }
                );
            },
            //
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            //
            handleCurrentChange(val){
                this.currentpage = val;
                this.ondispatch();
            },
			//        	
            isClear(formName){
                this.$refs[formName].resetFields();
                this.keywords ='';
                this.start_time = '';
                this.end_time='';
                this.ondispatch();
            },

        	// 
            onFilter(column){
                this.filter = Object.values(column)[0];
                this.ondispatch();
            },

        	// 
            onSearch(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.keywords = this.ruleForm.keywords;
                        if(this.ruleForm.applyDate[0] =='' || this.ruleForm.applyDate[0] == null || this.ruleForm.applyDate[1] =='' || this.ruleForm.applyDate[1] ==null){

                        }else{
                            this.start_time = this.ruleForm.applyDate[0].Format("yyyy-MM-dd");
                            this.end_time = this.ruleForm.applyDate[1].Format("yyyy-MM-dd");
                        }
                        this.ondispatch();
                    } else {
                        return false;
                    }
                });
            },

            // 
            Tolink(row){
            	this.$router.push({path:'/hire/resume', query:{talent_id: row.talent_id} });
            },

            // 
            resumeAction(row, val, type='approval') {
            	this.talentId = row.talent_id;
            	this.currentStatus = row.status_value;
            	this.approvalValue = val;

            	if (! val) { //
            		this.approvalFormVisible = true;	
            		this.dialogTitle = '';
            		this.dialogContent = '';
            	} else {
            		if (type === 'approval') { //
	            		switch(row.status_value) {
	            			case 1:
			            		this.approvalFormVisible = true;	
			            		this.dialogTitle = '';
			            		this.dialogContent = '';
	            				break;
	            			case 2:
			            		this.approvalFormVisible = true;	
			            		this.dialogTitle = '';
			            		this.dialogContent = '';
	            				break;
	            		}
            		} 
        			if (type === 'join-talent') { //
	            		this.jointalentFormVisible = true;	
	            		this.dialogTitle = '';
	            		this.dialogContent = '';
        			}
        			if (type === 'employ') { //
	            		this.employFormVisible = true;	
	            		this.dialogTitle = '';
	            		this.dialogContent = '';
        			}
            	}
            },

            // 
            hideDialog(name){
            	this[name] = false;
            }
        }
   	}
</script>



