<template>
	<section class="resume-section">
		<header class="section-header">
			<el-row>
				<el-col :xs="12" :sm="12" :md="12">
					<h2> <small>{{basicInfo.status}}</small></h2>
				</el-col>
				<el-col class="t-r actions" :xs="12" :sm="12" :md="12">
					<a href="javascript:void(0)" @click="edit" title=""><i class="el-icon-edit"></i></a>
				</el-col>
			</el-row>	
		</header>

		<div class="section-content">
			<el-row>
				<el-col :xs="24" :sm="16" :md="16">
					<el-row>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span></span>{{basicInfo.name}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span></span>{{basicInfo.sex==1?'':''}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span></span>{{basicInfo.phone}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span></span>{{basicInfo.birthday}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span></span>{{basicInfo.email}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span></span>{{basicInfo.age}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span></span>{{basicInfo.nation}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span></span></p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span></span>{{basicInfo.political}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span></span></p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span></span>{{basicInfo.work_time}}</p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span></span></p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span></span></p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span></span></p>
						</el-col>
						<el-col :xs="24" :sm="12" :md="12">
							<p class="item"><span></span>{{basicInfo.daogang}}</p>
						</el-col>
					</el-row>
				</el-col>
			</el-row>
		</div>
	</section>
</template>

<script>
	export default {
		props:[ 'resumeId' ],
	    data() {
	    	return {
	    		formData: {

	    		},
	    	}
	    },
	    computed: {
	    	basicInfo(){
	    		return this.$store.state.resume.basic_info;
	    	}
	    },
	    created(){
	    	this.$store.dispatch('Basic_Info',{id:this.resumeId}).then(response => {
	    		let data = response.data.data;
	    		this.$emit('setName', data.name);
	    	});
	    },
	    methods: {
	    	//
	    	edit(){
	    	}
	    }
	}
</script>

<style res="stylesheet/less" lang="less" scoped>
	@import './style.less';
	
	.photo {
	}
</style>


