export const DateTime =() =>{
    const date = new Date();
    const year = date.getFullYear()+'';
    const month = date.getMonth()+1+'';
    const day = date.getDate()+'';
    const dateTime = year + month + day;
    return dateTime;
}

/**
 * 
 */

Date.prototype.Format = function (fmt) { //author: meizz
    var o = {
        "M+": this.getMonth() + 1, //
        "d+": this.getDate(), //
        "h+": this.getHours(), //
        "m+": this.getMinutes(), //
        "s+": this.getSeconds(), //
        "q+": Math.floor((this.getMonth() + 3) / 3), //
        "S": this.getMilliseconds() //
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
        if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
}


/**  **/
export const SpendTime =(begin, end) => {
	let diff_time, days, hours, minutes, string = '';
	if (typeof begin === 'number') {
		diff_time = begin;
	} else {
		let begin_time = (new Date(begin)).getTime(), end_time;
		if (typeof end === 'undefined')
			end_time = (new Date()).getTime();
		else
			end_time = (new Date(end)).getTime();

		diff_time = (end_time-begin_time) / 1000;
	}

	days = Math.floor(diff_time/86400);
	hours = Math.floor( (diff_time%86400) / 3600 );
	minutes = Math.floor( (diff_time%3600) / 60 );

	if (days > 0) string += days+'';
	if (days > 0 || hours > 0) string += hours+'';
	string += minutes+'';

	return string;
}

export const spendTime = (time)=>{
    let  days, hours, minutes, string = '';
    days = Math.floor(time/86400);
    hours = Math.floor( (time%86400) / 3600 );
    minutes = Math.floor( (time%3600) / 60 );
    if (days > 0) string += days+'';
    if (days > 0 || hours > 0) string += hours+'';
    string += minutes+'';
    return string;
}

//
export const digitUppercase = function(n) {
    var fraction = ['', ''];
    var digit = [
        '', '', '', '', '',
        '', '', '', '', ''
    ];
    var unit = [
        ['', '', ''],
        ['', '', '', '']
    ];
    var head = n < 0 ? '' : '';
    n = Math.abs(n);
    var s = '';
    for (var i = 0; i < fraction.length; i++) {
        s += (digit[Math.floor(n * 10 * Math.pow(10, i)) % 10] + fraction[i]).replace(/./, '');
    }
    s = s || '';
    n = Math.floor(n);
    for (var i = 0; i < unit[0].length && n > 0; i++) {
        var p = '';
        for (var j = 0; j < unit[1].length && n > 0; j++) {
            p = digit[n % 10] + unit[1][j] + p;
            n = Math.floor(n / 10);
        }
        s = p.replace(/(.)*$/, '').replace(/^$/, '') + unit[0][i] + s;
    }
    return head + s.replace(/(.)*/, '')
            .replace(/(.)+/g, '')
            .replace(/^$/, '');
};


