<template>
	<div class="container">
        <h3 class="mb-md"></h3>

	    <el-form :model="ruleForm" ref="ruleForm">
            <el-row>
                <el-col :xs="24" :md="16">
                    <el-row :gutter="10" class="search-row">
                        <el-col :xs="24" :sm="10" :md="10" class="search-label">
                            <el-form-item prop="keywords">
                                <el-input v-model="ruleForm.keywords" placeholder=""></el-input>
                            </el-form-item>
	                        <button type="button" class="search-icon el-icon-search" @click="onSearch"></button>
                        </el-col>
                        <el-col :xs="24" :sm="14" :md="14"> 
							<el-button type="primary" @click="newStaff"></el-button>
							<el-button type="primary" @click="dataSync" :loading="syncing"></el-button>
                        </el-col>
                    </el-row>
                </el-col>
            </el-row>
        </el-form>

        <el-row> 
            <el-col :xs="24" :md="6">
				<el-tree :data="org" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
            </el-col>
			<el-col :xs="24" :md="18">				
		        <el-row>
					<el-table :data="tableData" border style="width: 100%">
						<el-table-column type="index" label="" width="80"></el-table-column>
						<el-table-column prop="empno" label="" width="100"></el-table-column>
						<el-table-column prop="name" label="" width="100"></el-table-column>
						<el-table-column prop="phone" label="" width="120"></el-table-column>
						<el-table-column prop="entry" label="" width="100"></el-table-column>
						<el-table-column prop="employee_type" label="" width="100"></el-table-column>
						<el-table-column prop="profession" label="" width="180"></el-table-column>
						<el-table-column prop="entry_time" label="" width="120"></el-table-column>
						<el-table-column prop="leave_time" label="" width="120"></el-table-column>
						<el-table-column label="" width="130">
							<template scope="scope">
								<div class="t-r">
									<el-button @click="entry(scope.row)" type="text" size="small" v-if="scope.row.entry_status==0">  </el-button>	
									<el-button @click="Tolink(scope.row)" type="text" size="small">  </el-button>	
								</div>
							</template>
						</el-table-column>
					</el-table>
				</el-row>
		        <el-row class="t-r mt-lg">
		            <el-pagination v-if="pagenation == 1"
		                    @size-change="handleSizeChange"
		                    @current-change="handleCurrentChange"
		                    :current-page="currentpage"
		                    :page-sizes="[10, 20, 30, 40]"
		                    :page-size="pageSize"
		                    layout="total, sizes, prev, pager, next, jumper"
		                    :total="total">
		            </el-pagination>
		            <el-pagination v-else small
		                    @size-change="handleSizeChange"
		                    @current-change="handleCurrentChange"
		                    :current-page="currentpage"
		                    :page-sizes="[10, 20, 30, 40]"
		                    :page-size="pageSize"
		                    layout="prev, pager, next"
		                    :total="total">
		            </el-pagination>
		        </el-row>
			</el-col>
        </el-row>

		<new-staff 
			:org="org"
			:newstaffFormVisible="newstaffFormVisible"
			@hideDialog="hideDialog"
			@refresh="ondispatch"
		></new-staff><!--  -->
		<staff-entry
			:staffId="staffId"
			:staffName="staffName"
			:staffentryFormVisible="staffentryFormVisible"
			@hideDialog="hideDialog"
			@refresh="ondispatch"
		></staff-entry><!--  -->
    </div>
</template>

<script>
	import { mapGetters } from 'vuex';
	import newStaff from '../../components/staff/newStaff.vue';
	import staffEntry from '../../components/staff/entry.vue';

	export default {
        components: { newStaff, staffEntry  },
        name:'salary',
        data(){
            return {
                ruleForm:{
                    keywords:'',
                },
				defaultProps: {
					children: 'children',
					label: 'label'
				},
				org_id: '',
				syncing: false,

				currentpage: 1,
                pageSize:10,
                total:0,
                keywords:'',

                newstaffFormVisible: false,
                staffentryFormVisible: false,

                staffId: '',
                staffName: ''
            }
        },
        computed:{
            ...mapGetters([
                'pagenation'
			]),
			syncUrl(){
				return this.$store.state.global.serverUrl+'/third_api/quanxian/index?api=users';
			},
	    	org(){
	    		return this.$store.state.global.organizationalStructure;
	    	},
	    	tableData(){
	    		return this.$store.state.staff.staff_list;
	    	}
        }, 
        created(){        	
            this.$store.dispatch('OrganizationalStructure');
            this.$store.dispatch('Staff_List');
        },
	    methods: {
	    	//
            searchData(paged){
                if(paged != null){
                    this.currentpage = paged;
                }
                return {
                    page:this.currentpage,
                    page_size:this.pageSize,
                    keywords:this.keywords,
                    org_id: this.org_id
                };            	
            },
        	// 
            onSearch(formName){
                this.keywords = this.ruleForm.keywords;
                this.ondispatch();
            },
            //
            handleSizeChange(val){
                this.pageSize = val;
                this.ondispatch();
            },
            //
            handleCurrentChange(val){
                this.currentpage = val;
                this.ondispatch();
            },
            //
            ondispatch(){
                this.$store.dispatch('Staff_List',this.searchData()).then(response =>{
	                let data = response.data.data;
	                if (response.data.code == 200) {
		                this.currentpage = data.page.currentPage;
		                this.total = data.page.totalCount;
	                }
                });
            },

            //
            newStaff(){
            	this.newstaffFormVisible = true;
            },
            //
            entry(row){
            	this.staffId = row.employee_id;
            	this.staffName = row.name;
            	this.staffentryFormVisible = true;
            },


            //
            dataSync(){
            	this.syncing = true;
                fetch(this.syncUrl).then(response => {
	            	this.syncing = false;
	            	this.ondispatch();
                }).catch(error => {
	            	this.syncing = false;
                });
            },
            //
            handleNodeClick(data){
            	this.org_id = data.value;
            	this.ondispatch();
            },

            //
            Tolink(row){
            	this.$router.push({path:'/hire/detail', query:{staff_id: row.employee_id} });
            },

            //
            hideDialog(name){
            	switch (name){
            		case 'newStaff':
            			this.newstaffFormVisible = false;
            			break;
            		case 'entry':
            			this.staffentryFormVisible = false;
            			break;
            	}
            }

	    }

	}	
</script>

<style rel="stylesheet/less" lang="less" scoped>
	.el-tree {
		border: none;
		.el-tree-node {
			font-size: 12px;
		}
	}
	.upload-control {
		width: 88px;
		display: inline-block;
	}
	.search-row {
		margin-bottom: 0;
	}
	.search-label {
		position: relative;

		.search-icon {
			position: absolute;
			right: 10px;
			top: 3px;
			width: 28px;
			height: 28px;
			font-size: 20px;
			line-height: 28px;
			color: #999;
			background: none;
			border: none;
			&:focus {
				outline: none;
			}
		}
	}
</style>
<style rel="stylesheet/css" lang="css">
	.el-tree-node__label {
		font-size: 12px;
	}
	.el-tree-node__content:hover,
	.is-current .el-tree-node__content {
	    background: #f1f1f1;
	}
</style>


