/**  **/
import fetch from '../utils/fetch';
import qs from 'qs';

//
export function talent_Types() {
	return fetch({
        url: '/oa_v1/talent/get-person-type',
        method: 'get'
    });
}

//
export function edu_List() {
	return fetch({
        url: '/oa_v1/talent/get-edu',
        method: 'get'
    });
}

//
export function resume_List(params) {
	return fetch({
        url: '/oa_v1/talent/get-list',
        method: 'get',
        params: params
    });
}

//
export function resume_Add(params) {
	return fetch({
        url: '/oa_v1/talent/add-zhaopin',
        method: 'post',
        data: qs.stringify(params)
    });
}

//
export function resume_Approval(params) {
	return fetch({
        url: '/oa_v1/talent/approval',
        method: 'post',
        data: qs.stringify(params)
    });
}

//
export function join_Talent(params) {
	return fetch({
        url: '/oa_v1/talent/join-talent',
        method: 'post',
        data: qs.stringify(params)
    });
}

//
export function employ_Talent(params) {
	return fetch({
        url: '/oa_v1/talent/employ',
        method: 'post',
        data: qs.stringify(params)
    });
}

//
export function salary_List(params) {
    return fetch({
        url: '/oa_v1/salary/get-list',
        method: 'get',
        params: params
    });
}

//token
export function salary_Token(params) {
    return fetch({
        url: '/oa_v1/salary/get-token',
        method: 'post',
        data: qs.stringify(params)
    });
}

//
export function staff_List(params) {    
    return fetch({
        url: '/oa_v1/employee/get-list',
        method: 'get',
        params: params
    });
}

//
export function staff_Add(params) {    
    return fetch({
        url: '/oa_v1/employee/add-employee',
        method: 'post',
        data:qs.stringify(params)
    });
}

//
export function staff_Entry(params) {    
    return fetch({
        url: '/oa_v1/employee/entry',
        method: 'post',
        data:qs.stringify(params)
    });
}

//
export function  Positive_Create(params) {
    return fetch({
        url: '/oa_v1/apply-positive/add-apply',
        method: 'post',
        data:qs.stringify(params)
    });
}

//
export function  Transfer_Create(params) {
    return fetch({
        url: '/oa_v1/apply-transfer/add-apply',
        method: 'post',
        data:qs.stringify(params)
    });
}

//
export function applyLeave_Handover() {
	return fetch({
        url: '/oa_v1/apply-leave/get-list',
        method: 'get'
    });
}

//
export function applyLeave_Create(params) {
    return fetch({
        url: '/oa_v1/apply-leave/add-apply ',
        method: 'post',
        data:qs.stringify(params)
    });
}



